.card {
  display: flex;
  justify-content: center;
  width: 90%;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  flex-wrap: wrap;
  text-align: center;
}
.spanflex {
  flex-basis: 30%;
}
.spanflex img {
  border-radius: 50px;
  padding: 20px;
  width: 100%;
  aspect-ratio: 1.4;
}

.headcollab h3 {
  text-align: center;
  margin: 40px 0 20px 0;
  font-size: 34px;
}

@media (max-width: 999px) {
  .card {
    flex-direction: column;
  }
}
